@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Work Sans', sans-serif;
  min-width: 350px;

  --color-1: #2A6F97;
  --color-2: #e9ecef;
  --color-3: #E0FBFC;
  --color-4: #EE6C4D;
  --color-5: #293241;
  --color-6: #02203C;
  --color-7: #15314B;
  --color-8: #7411ad;
  --color-9: #a0a510;
  --color-10: #ad6111;
  --color-11: #2078d6;
  --color-12: gray;
  --color-13: #445d6e;
  --color-14: #f6f9fc;
}

.header {
    display: flex;
    justify-content: center;
    padding: 20px;
    background: var(--color-6);
    color: #FFFFFF;
}

.header-first-part {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 500px;
    height: 350px;
}

.header-first-part p {
    font-size: 18px;
    line-height: 1.7em;
    font-weight: 600;
}

.header-second-part {
    width: 500px;
    display: flex;
    justify-content: flex-end;
    position: relative;
    margin-top: 30px;
    perspective: 700px;
}

.header-info-box {
    width: 300px;
    height: 400px;
    background: var(--color-6);
    position: absolute;
    transform: rotate3d(.5, -.866, 0, 15deg) rotate(-1deg);
    border: 1px solid var(--color-7);
    border-radius: 4px;
    box-shadow: 0 38px 40px -17.5px rgba(0, 0, 0, .4), 46px 38px 70px -7.5px rgba(0, 0, 0, .5);
}

.header-profile-title {
    background: var(--color-7);
    padding: 5px;
    color: #FFFFFF;
    font-weight: bold;
}

.header-profile-content {
    padding: 10px;
}

.header-profile-img {
    display: flex;
    justify-content: center;
}

.header-profile-img > img {
    border-radius: 50%;
    border: 1px solid var(--color-7);
}

.header-profile-info {
    width: calc(100% - 10px);
    height: 130px;
    margin-top: 10px;
    padding: 5px;
    background: var(--color-7);
    font-family: Source Code Pro, Helvetica, Arial, sans-serif;
    font-size: 15px;
    color: var(--color-12);
}

.object-type {
    color: var(--color-8);
}

.object-name {
    color: var(--color-9);
}

.reserved {
    color: var(--color-10);
}

.attribute {
    color: var(--color-11);
}

@media screen and (max-width: 710px) {
    .header {
        flex-direction: column;
        height: 600px;
        justify-content: flex-start;
    }

    .header-first-part {
        width: 100%;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        text-align: center;
        justify-content: flex-start;
    }

    .header-second-part {
        width: 100%;
        justify-content: center;
    }
}

@media screen and (max-width: 390px) {
    .header {
        height: 700px;
    }
}

.timeline-box {
    padding: 10px 40px;
    position: relative;
    width: calc(50% - 80px);
}

.timeline-box::after {
    content: '';
    position: absolute;
    top: 15px;
    right: -20px;
    width: 30px;
    height: 30px;
    background: var(--color-14);
    border-radius: 50%;
    border: 4px solid var(--color-11);
}

.left {
    left: 0;
}

.right {
    left: 50%;
}

.right::after {
    left: -19px;
}

.left::before {
    content: '';
    height: 0;
    width: 0;
    position: absolute;
    top: 22px;
    right: 30px;
    border: solid var(--color-14);
    border-width: 10px 0 10px 10px;
    border-color: transparent transparent transparent var(--color-14);
}

.right::before {
    content: '';
    height: 0;
    width: 0;
    position: absolute;
    top: 22px;
    left: 30px;
    border: solid var(--color-14);
    border-width: 10px 10px 10px 0;
    border-color: transparent var(--color-14) transparent transparent;
}

.timeline-content {
    padding: 20px 30px;
    background: var(--color-14);
    border-radius: 6px;
}

.timeline-content > span {
    font-size: 1.17em;
    font-weight: bold;
}

.timeline-content span, .timeline-content p {
    color: #445d6e;
}

.left-content {
    border-left: 5px solid var(--color-11);
}

.right-content {
    border-right: 5px solid var(--color-11);
}

@media screen and (max-width: 800px) {
    .timeline-box {
        width: calc(100% - 100px);
        padding-left: 70px;
        padding-right: 25px;
    }

    .timeline-box::before {
        left: 60px;
        border-width: 10px 10px 10px 0;
        border-color: transparent var(--color-3) transparent transparent;
    }

    .left::after, .right::after {
        left: 12px;
        z-index: 1;
    }

    .right {
        left: 0;
    }

    .left-content {
        border-left: none;
        border-right: 5px solid var(--color-11);
    }
}

.timeline {
    padding: 20px 0;
    background: var(--color-6);
    font-family: Open Sans, Helvetica, Arial, sans-serif;
    line-height: 1.7;
}

.timeline-container {
    max-width: 1200px;
    margin: 40px auto;
    position: relative;
}

.timeline-container::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    width: 6px;
    background: var(--color-14);
    left: 50%;
    margin-left: -3px;
    border-radius: 5px;
}

@media screen and (max-width: 800px) {
    .timeline-container::before {
        left: 31px;
    }
}

.session-title {
    text-align: center;
    width: 50%;
    padding: 30px;
    margin: 0 auto;
    font-family: 'Ubuntu', sans-serif;
    font-size: 35px;
    border-bottom: 1px solid;
}

.skill-content {
    text-align: center;
    width: 60px;
    height: 100px;
    padding: 20px 60px 0 60px;
    margin: 0 10px;
    font-size: 15px;
    border-radius: 8px;
    transition: all 0.3s;
}

.skill-content:hover {
    box-shadow: 0 0 35px -2px rgba(0,0,0,.2);
}

@media screen and (max-width: 430px) {
    .skill-content {
        margin: 0 5px;
        padding-left: 30px;
        padding-right: 30px;
    }
}

.skills-container {
    padding-bottom: 30px;
    padding-top: 120px;
    background: var(--color-14);
}

.skills {
    max-width: 900px;
    margin: 0 auto;
    text-align: center;
    color: var(--color-13);
}

.skills p {
    font-size: 18px;
    margin: 30px 20px 20px 10px;
}

.skill-box {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 15px;
}

.contacts-container {
    text-align: center;
    padding: 20px 0 60px 0;
}

.contacts-container h4 {
    font-weight: lighter;
}

.contacts-content {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.contacts-content a {
    text-decoration: none;
    color: #000000;
    padding: 20px 10px;
    border-radius: 8px;
    margin: 0 10px;
    transition: all 0.3s;
}

.contacts-content a:hover {
    box-shadow: 0 0 35px -2px rgba(0,0,0,.2);
}

.contacts-content img {
    width: 60px;
    height: 60px;
    margin: 0 25px;
}

.email-session {
    display: flex;
    justify-content: center;
    align-items: center;
}

.email-session img {
    cursor: pointer;
}

#email {
    background: none;
    border: none;
    font-size: 16px;
    color: black;
    font-family: 'Work Sans', sans-serif;
}

#email:focus {
    outline-style: none;
    box-shadow: none;
    border-color: transparent;
}

.tooltip {
    text-transform: uppercase;
    padding: 5px 10px;
    background: var(--color-6);
    color: #FFFFFF;
    border-radius: 3px;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.4s, margin-left 0.4s;
    width: 100px;
    margin: 0 auto;
}

.tooltip.show {
    opacity: 1;
    pointer-events: auto;
    margin: 0 auto;
}


.projects-session {
    background: var(--color-2);
    padding-bottom: 40px;
}

.projects-session a {
    background: var(--color-1);
    text-decoration: none;
    color: #FFFFFF;
    padding: 0 3px;
}

.projects-box {
    text-align: center;
    padding: 0 20px;
}

.project {
    max-width: 600px;
    margin: 0 auto;
    padding-top: 10px;
}

.project img {
    width: 100%;
}

